export function transformName(customer) {
  return `${customer.surname} ${customer.name} ${customer.middleName}`;
}

export function transformPhone(customer) {
  const phone = customer.phonenumber;
  if (phone) {
    return `
        ${phone.slice(0, 2)} 
        ${phone.slice(2, 5)} 
        ${phone.slice(5, 8)} 
        ${phone.slice(8, 10)}
        ${phone.slice(11)}`;
  }
  return '';
}

export function transformRoomInfo(order) {
  let night;
  switch (+order.numberOfNights) {
    case 1: {
      night = '';
      break;
    }
    case 2:
    case 3:
    case 4: {
      night = '';
      break;
    }
    default: {
      night = '';
      break;
    }
  }

  return ` "${order.typeOfRoom}"  ${order.numberOfNights} ${night}`;
}

export function transformCountOfPeople(order) {
  let adult = (+order.numberOfAdults === 1) ? '' : '';
  adult = `${+order.numberOfAdults} ${adult}`;
  let teenager = '';
  let child = '';
  function checkChildrenCount(count) {
    switch (+count) {
      case 1: {
        return '';
      }
      case 2:
      case 3:
      case 4: {
        return '';
      }
      default: {
        return '';
      }
    }
  }

  if (+order.numberOfTeenagers > 0) {
    teenager = `, ${(order.numberOfTeenagers)} ${(checkChildrenCount(order.numberOfTeenagers))}  12 `;
  }

  if (+order.numberOfChildren > 0) {
    child = `  ${(order.numberOfChildren)} ${checkChildrenCount(order.numberOfChildren)}  12 `;
  }
  return adult + teenager + child;
}

export function transformInsurance(order) {
  return (
    (order.insurance) ? ' ' : '  '
  );
}
